body {
    font-family: Verdana;
}

.input {
  position: relative;
  font-size: 16px;
  margin-bottom: 16px;
}

/* Normal state */
.input input {
  min-width: 240px;
  padding: 10px 16px;
  border-radius: 3px;
  border: none;
  -webkit-box-shadow: 0 0 0 1px #95a5a6;
          box-shadow: 0 0 0 1px #95a5a6;
}
.input input::-webkit-input-placeholder  {
  opacity: 0;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
}
.input input::-moz-placeholder {
  opacity: 0;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
}
.input label {
  color: #95a5a6;
  position: absolute;
  top: 10px;
  left: 16px;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}

/* Focussed state  */
.input input:focus {
  outline: none;
  -webkit-box-shadow: 0 0 0 1px #3498db;
          box-shadow: 0 0 0 1px #3498db;
}
.input input:focus::-webkit-input-placeholder {
  opacity: 0.5;
}
.input input:focus::-moz-placeholder {
  opacity: 0.5;
}
.input input:focus ~ label,
.input input:not(:placeholder-shown) ~ label {
  background-color: #fff;
  padding: 1px 2px;
  border-radius: 3px;
  left: 0;
  -webkit-transform: translate3d(0,-21px,0) scale(0.75);
          transform: translate3d(0,-21px,0) scale(0.75)
}
.input input:focus ~ label {
  color: #3498db;
}

/* Error state */
.input input:invalid:not(:placeholder-shown) {
  -webkit-box-shadow: 0 0 0 1px #c0392b !important;
          box-shadow: 0 0 0 1px #c0392b !important;
}
.input input:invalid:not(:placeholder-shown) ~ label {
  color: #c0392b !important;
}


